#mapid {
  height: 100%;
  width: 100%;
}

.start{
  cursor: pointer;
}

.race-overview{
  font-family: 'Lato', sans-serif;
  font-weight: 300;
  position: absolute;
  top: 20px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  z-index: 1;
  background: #f5da3f;
  color: #000;
}

.race-overview p{
  text-align: center;
  font-size: 1.250rem;
  margin: 1em;
  letter-spacing: 0.1em;
}

.race-overview p span{
  display: block;
  margin: auto;
  font-size: 1.500rem;
  text-transform: uppercase;
  margin-top: 15px;
  font-weight: 700;
}

.race-selector{
  background: rgba(0, 0, 0, 0.1);
  color: #fff;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  z-index: 9999;
}

.navigator{
  position: absolute;
  width: 150%;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}
.navigator p{
  color: #fff;
  background: #000;
  display: inline-block;
  padding: 1em;
  margin: 0 -2.7em;
  cursor: pointer;
}

.navigator p + p{
  float: right;
}



